<template>
    <div class="mapForExpress" title="" style="background-color:#F9F9F9;">
        <navigation-bar class="titleBar"  @onLeftClick="onLeftClick" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
        <div v-if="isMapInfo">
            <div class="mapDiv" v-show="!isNetworkError">
                <template>
                    <div class="map">
                        <div id="GDMap" :style="{'height':isBackSocketError ? screeHeight+'px':'15rem'}">
                            <div class="shop-content-marker"></div>
                        </div>
                    </div>
                </template>
            </div>
            <div class="expressStatus" v-if="!isNetworkError && !isBackSocketError">
                <div class="touch-bar"></div>
                <div class="expressStatusMsgInFirstDiv">{{expressStatusMsgInFirstDiv}}</div>
                <div class="serviceMsg">{{serviceMsg}}</div>
                <div class="expressTime" v-if="isShowComputeTime">{{arriveTime}}</div>
                <div class="InfoDiv">
                    <div class="shopInfo" style="width: 100%" v-if="!isExpressStatus">
                        <img class="shopInfoImg" :src=shopPicurl>
                        <div class="shopName">{{shopInfo.shopName}}</div>
                        <img class="phoneBtn" src="../../assets/images/mall/express/button_contact_cut.png" @click="callShop(shopInfo.shopTel)">
                    </div>
                    <div class="expressInfo" style="width: 100%" v-else>
                        <img class="shopInfoImg" src="../../assets/images/mall/express/Delivery.png">
                        <div>
                            <div class="expressName">{{transporterName}}</div>
                            <img class="labelFlag" src="../../assets/images/mall/express/label_dada_cut.png">
                        </div>
                        <img class="phoneBtn" src="../../assets/images/mall/express/button_contact_cut.png" @click="callShop(transporterPhone)" v-if="transporterPhone!=''">
                    </div>
                </div>
            </div>
            <div class="expressDetail" v-if="!isNetworkError && !isBackSocketError">
                <div class="expressStatusCard" v-for="(item,i) in expressStatusShowData" :style="{'height':JSON.stringify(item.deliveryInfo) == '{}' ? '55px':'80px'}">
                    <img class="expressStatusIcon" :src=item.imgUrl>
                    <div class="expressTitle" :style="{color:item.textColor}">{{item.expressStatusTitle}}</div>
                    <div class="expressStatusMsg">{{item.expressStatusMsg}}</div>
                    <div class="deliveryInfo" style="margin-bottom: 10px" v-if="JSON.stringify(item.deliveryInfo) != '{}'">
                        <div class="deliveryName">{{item.deliveryInfo.transporterName}}</div>
                        <div class="deliveryPhone" @click="callShop(item.deliveryInfo.transporterPhone)">{{item.deliveryInfo.transporterPhone}}</div>
                    </div>
                    <div class="updateTime">{{item.updateTime}}</div>
                </div>
            </div>
        </div>
        <div class="expressMsgList" :style="padingtopNum"  v-if="!isNetworkError && !isMapInfo">
            <expressMsgList :subbillno='subbillno'></expressMsgList>
        </div>
        <div style="height:100%" v-if="isNetworkError ">
            <van-pull-refresh v-model="expressloading" @refresh="onRefresh" style="height: 100%">
                <div :style="{'height': screeHeight+'px'}">
                    <NetworkError style="padding-top: 50%"></NetworkError>
                </div>
            </van-pull-refresh>
        </div>

    </div>
</template>

<script>
    import {CouponCell, CouponList, Cell, CellGroup, Dialog} from 'vant';
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import NetworkError from '@/common/networkError/NetworkError';
    import { getDadaOrderTrack, getShunFengRouteInfo } from "../../services/expressTrajectory";
    // import AMap from 'AMap';
    import loadMap from '../../components/AMapLoader/loadMap'
    import expressMsgList from "../../components/expressMsgList/expressMsgList";
    export default {
        name: "mapForExpress",
        components:{
            "expressMsgList" :expressMsgList,
            NavigationBar,
            NetworkError,
        },
        data() {
            return {
                //
                title:'',
                headerItemWidth:{
                    width:'84px'
                },
                trackData:{},
                shopPosition:null,       //
                expressPosition:null,    //
                ownerPosition:null,      //
                transporterName:'--',    //
                transporterPhone:'',   //
                shopInfo: {}, //
                historyStatusDTOList:[],
                expressStatusShowData:[],
                arriveTime:'',   //
                ownerDistance:'',   //
                isShowComputeTime:false,
                expressStatusMsgInFirstDiv:'',
                serviceMsg:'',
                expressTime:'',
                shopPicurl:"",
                shopName:'',
                markers:[],
                isExpressStatus:false,//
                padingtopNum: { "padding-top": this.$stbHeight + 44 + "px"},
                expressData:{}, //
                subBillNo:'',
                subbillno:'',
                zoom: 14,
                lng: 0,
                lat: 0,
                key:'529ff32129046a07fd11ce5a2054a797',
                plugin: [
                    'AMap.Driving',
                    'AMap.Riding'
                ],
                //
                expressMap:null,
                //
                mapMarker:null,
                driving:null,
                graspRoad:null,
                deliveryImg:require('../../assets/images/mall/express/Delivery_cut.png'),
                positionPointUrl:require("../../assets/images/mall/express/position_cut.png"),
                deliveryContent:'' +
                    '<div class="custom-content-marker " id="shopContent" style="height:68px;width:60px;position: relative">' +
                    '   <img src="'+this.positionPointUrl+'" style="height: 80px;width: 70px;position: absolute">'+
                    '   <div class="deliveryMsg" style="height: 80px;width: 70px;position: absolute">'+
                    '<span style="font-family: PingFangSC-Medium;font-size: 12px;color: #333333;"></span>'+
                        '<span style="font-family: PingFangSC-Medium;font-size: 12px;color: #002429;">'+'{{this.ownerDistance}}'+'"|"'+'{{this.arriveTime}}'+'</span>'+
                    '</div>'+
                    '   <img src="'+this.deliveryImg+'" style="height: 48px;width: 48px;position: absolute; border-radius:40px;top: 10px;left: 10.5px">'
                    + '</div>',
                shopContent:'',
                isNetworkError:false,
                isBackSocketError:false,
                screeHeight: document.body.clientHeight,
                isMapInfo:true, //
                expressloading:false,
            }
        },
        mounted() {
            //
            console.log("mounted "+this.$route.params.subBillNo);
            if (this.$route.params.subBillNo !==null ) {
                this.subBillNo=this.$route.params.subBillNo;
            }
            if (this.$route.params.expressCompany){
                if (this.$route.params.expressCompany.indexOf('') != -1){
                    this.isMapInfo=true;
                    this.getDadaOrderTrackService(this.$route.params.subBillNo);
                }else if(this.$route.params.expressCompany.indexOf('') != -1){
                    this.subbillno=this.subBillNo;
                    console.log("  "+this.subbillno);
                    this.isMapInfo=false;
                }
            }else {
                this.isNetworkError=true;
            }
            loadMap()
                .then(AMap => {
                    this.expressMap = new AMap.Map('GDMap',{
                        zoom: 10,
                        center:[120.382689,36.066971],
                        animateEnable:true,
                        resizeEnable:true,
                    });
                    this.driving = new AMap.Driving({
                        map: this.expressMap,
                        showTraffic:false,
                        autoFitView:true,
                        hideMarkers:true,
                    });
                })
                .catch(() => {
                    this.isNetworkError=true;
                    console.log('')
                })

        },
        computed:{

        },
        methods:{
            onLeftClick(){
                this.$router.go(-1);
            },
            //
            markonMap(position,content){
                console.log(" "+position);
                console.log(" "+content);
                let marker = new AMap.Marker({
                    position:position,
                    // icon: icon,
                    content:content,
                    offset: new AMap.Pixel(-37, -32),
                });
                this.expressMap.add(marker);
            },
            markOwneronMap(position,ownerflag){
                var icon = new AMap.Icon({
                    size:new AMap.Size(60,65),
                    image:ownerflag==0 ? require('../../assets/images/mall/express/myposition_cut.png'):require('../../assets/images/mall/express/Delivery_cut.png'),
                    imageSize: new AMap.Size(60, 65)
                });
                console.log(" "+position);
                let marker = new AMap.Marker({
                    position:position,
                    icon: icon,
                    offset: new AMap.Pixel(-25, -45),
                });
                this.expressMap.add(marker);
            },
            //
            drawLine(startPositon,endPostion,deliveryStatus=0){
                let _this = this;
                let isSetFocus = false;
                AMap.plugin('AMap.Driving', function() {
                    var driving = new AMap.Driving({
                        showTraffic: false,
                        autoFitView: false,
                        hideMarkers: true,
                    });
                    driving.search(startPositon, endPostion, function (status, result) {
                        if (status === 'complete') {
                            console.log(result);
                            let strokeColor = '#FC5465';
                            let strokeStyle = 'solid';
                            if (result.routes && result.routes.length) {
                                if (deliveryStatus===1){   //
                                    let currentTime=new Date();
                                    let calcDay = currentTime.getTime()+result.routes[0].time*1000;
                                    let calcTime = new Date(calcDay);
                                    let res = calcTime.getDay()-currentTime.getDay();
                                    if (res==0){
                                        _this.arriveTime=calcTime.getHours()+":00 - "+(calcTime.getHours()+1)+':00';
                                    }else if (res==1){
                                        _this.arriveTime='';
                                    }else if (res==2){
                                        _this.arriveTime='';
                                    } else if (res>2){
                                        _this.arriveTime='';
                                    }else {
                                        _this.isShowComputeTime=false;
                                    }
                                    _this.ownerDistance=result.routes[0].distance;
                                    let distance = result.routes[0].distance>1000 ? (result.routes[0].distance/1000).toFixed(2)+"km":result.routes[0].distance+"m";
                                    let spandTime=result.routes[0].time<60?"":(result.routes[0].time/60).toFixed(0)+'';
                                    let contentwidth = result.routes[0].time<60 ? 160:(result.routes[0].time > 5969 ? 155 : 140); //
                                    let letDistance = result.routes[0].time<60 ? -50:(result.routes[0].time > 5969 ? -40 : -35);  //
                                    var deliveryImg=require("../../assets/images/mall/express/Delivery_cut.png");
                                    var positionPointUrl=require("../../assets/images/mall/express/position_cut.png");
                                    console.log("this.arriveTime"+_this.arriveTime);
                                    _this.deliveryContent='' +
                                        '<div class="custom-content-marker " id="shopContent" style="height:68px;width:60px;position: relative">' +
                                        '<div style="height: 25px;width:'+contentwidth+'px;background: #FFFFFF;border-radius: 20px;font-size: 12px;position: absolute;top: -20px;left: '+letDistance+'px;text-align: center">'+
                                        '<p style="position: absolute;top: -7px;left: 10px"></p>'+'<p style="font-family: PingFangSC-Medium;color: #FC5465;position: absolute;top: -7px;left: 38px;">'+distance+'|'+spandTime+'</p>'+'</div>'+
                                        '   <img src="'+positionPointUrl+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '   <img src="'+deliveryImg+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '</div>';
                                    _this.markonMap(_this.expressPosition,_this.deliveryContent);
                                    isSetFocus=true;
                                }else if (deliveryStatus===2){  //
                                    var deliveryImg=require("../../assets/images/mall/express/Delivery_cut.png");
                                    var positionPointUrl=require("../../assets/images/mall/express/position_cut.png");
                                    _this.deliveryContent='' +
                                        '<div class="custom-content-marker " id="shopContent" style="height:68px;width:60px;position: relative">' +
                                        '<div style="height: 25px;width: 60px;background: #FFFFFF;border-radius: 20px;font-size: 12px;position: absolute;top: -18px;left: 5px">'+
                                        '<p style="position: absolute;top: -7px;left: 2px"></p>'+'</div>'+
                                        '   <img src="'+positionPointUrl+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '   <img src="'+deliveryImg+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '</div>';
                                    _this.markonMap(_this.ownerPosition,_this.deliveryContent);
                                    strokeColor = '#FFB6BD';
                                    isSetFocus=true;
                                }else if(deliveryStatus===3){ //
                                    var deliveryImg=require("../../assets/images/mall/express/Delivery_cut.png");
                                    var positionPointUrl=require("../../assets/images/mall/express/position_cut.png");
                                    _this.deliveryContent='' +
                                        '<div class="custom-content-marker " id="shopContent" style="height:68px;width:60px;position: relative">' +
                                        '<div style="height: 25px;width: 60px;background: #FFFFFF;border-radius: 20px;font-size: 12px;position: absolute;top: -18px;left: 5px">'+
                                        '<p style="position: absolute;top: -7px;left: 10px"></p>'+'</div>'+
                                        '   <img src="'+positionPointUrl+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '   <img src="'+deliveryImg+'" style="height: 100%;width: 100%;position: absolute">'+
                                        '</div>';
                                }else if (deliveryStatus ===4){  //
                                    strokeColor = '#FFB6BD';
                                }
                                // 
                                var path = _this.parseRouteToPath(result.routes[0]);
                                var routeLine = new AMap.Polyline({
                                    path: path,
                                    isOutline: false,   //	
                                    strokeWeight: 7,   //
                                    strokeColor: strokeColor,  //
                                    strokeStyle:strokeStyle,//
                                    lineJoin: 'bevel',  //
                                });
                                _this.expressMap.add(routeLine);
                                // routeLine.setMap(_this.expressMap);
                                if (isSetFocus){
                                    _this.expressMap.setFitView([routeLine],false);
                                }
                                // 
                                console.log('')
                            }
                        } else {
                            console.log('' + result)
                        }
                    });
                })
            },
            parseRouteToPath(route) {
                var path = []
                for (var i = 0, l = route.steps.length; i < l; i++) {
                    var step = route.steps[i]
                    for (var j = 0, n = step.path.length; j < n; j++) {
                        path.push(step.path[j])
                    }
                }
                return path
            },
            resolveTrackData(trackData){
                if (trackData.statusCode == "1"){//
                    this.expressStatusMsgInFirstDiv='';
                    this.serviceMsg='';
                    this.isExpressStatus=false;
                    this.shopPosition = [Number(trackData.latFormatList[0].lng), Number(trackData.latFormatList[0].lat)];   //
                    this.expressMap.setZoomAndCenter(14,this.shopPosition);
                    this.markonMap(this.shopPosition,this.shopContent);
                }else if(trackData.statusCode == "2" || trackData.statusCode == "3"){  // 
                    this.expressStatusMsgInFirstDiv = trackData.statusCode==2 ? "":"";
                    this.serviceMsg=trackData.statusCode=="2" ? '':'';
                    this.isExpressStatus=true;
                    this.shopPosition=new AMap.LngLat(Number(trackData.latFormatList[0].lng),Number(trackData.latFormatList[0].lat));
                    this.expressPosition=new AMap.LngLat(Number(trackData.latFormatList[1].lng),Number(trackData.latFormatList[1].lat));
                    this.ownerPosition=trackData.statusCode=="2" ? null:new AMap.LngLat(Number(trackData.latFormatList[2].lng),Number(trackData.latFormatList[2].lat));
                    this.transporterName=trackData.transporterName;
                    this.transporterPhone=trackData.transporterPhone;
                    this.expressMap.setZoomAndCenter(14,this.shopPosition);
                    this.markonMap(this.shopPosition,this.shopContent);
                    if (trackData.statusCode=="2"){
                        this.markOwneronMap(this.expressPosition,1);
                        this.drawLine(this.shopPosition,this.expressPosition);
                        this.expressMap.setFitView();
                    } else if (trackData.statusCode=="3") {
                        this.markOwneronMap(this.ownerPosition,0);
                        this.drawLine(this.expressPosition,this.ownerPosition,1);//content
                        this.drawLine(this.shopPosition,this.expressPosition,4);
                        this.isShowComputeTime=true;
                    }
                }else if (trackData.statusCode == "100"){ //
                    this.$toast("");
                    this.expressStatusMsgInFirstDiv =  "";
                    this.serviceMsg= '';
                    this.shopPosition=new AMap.LngLat(Number(trackData.latFormatList[0].lng),Number(trackData.latFormatList[0].lat));
                    this.expressPosition=new AMap.LngLat(Number(trackData.latFormatList[1].lng),Number(trackData.latFormatList[1].lat));
                    this.transporterName=trackData.transporterName;
                    this.transporterPhone=trackData.transporterPhone;
                    this.markonMap(this.shopPosition,this.shopContent);
                    this.drawLine(this.shopPosition,this.expressPosition,3);
                    this.expressMap.setZoomAndCenter(14,this.ownerPosition);
                } else if (trackData.statusCode == "4"){  //
                    this.$toast("");
                    this.isExpressStatus=true;
                    this.expressStatusMsgInFirstDiv =  "";
                    this.serviceMsg= '';
                    this.shopPosition=new AMap.LngLat(Number(trackData.latFormatList[0].lng),Number(trackData.latFormatList[0].lat));
                    this.ownerPosition=new AMap.LngLat(Number(trackData.latFormatList[1].lng),Number(trackData.latFormatList[1].lat));
                    this.transporterName=trackData.transporterName;
                    this.transporterPhone=trackData.transporterPhone;
                    this.markonMap(this.shopPosition,this.shopContent);
                    this.markOwneronMap(this.ownerPosition,0);
                    this.drawLine(this.shopPosition,this.ownerPosition,2);//content
                }else if(trackData.statusCode=="9"){
                    this.$toast("");
                    this.expressStatusMsgInFirstDiv='';
                    this.serviceMsg='';
                    this.transporterName=trackData.transporterName;
                    this.transporterPhone=trackData.transporterPhone;
                    this.isExpressStatus=false;
                    this.shopPosition = [Number(trackData.latFormatList[0].lng), Number(trackData.latFormatList[0].lat)];
                    this.markonMap(this.shopPosition,this.shopContent);
                    if(trackData.latFormatList.length>1){
                        this.expressPosition=new AMap.LngLat(Number(trackData.latFormatList[1].lng),Number(trackData.latFormatList[1].lat));
                        this.markOwneronMap(this.expressPosition,1);
                        this.drawLine(this.expressPosition,this.shopPosition);
                        this.expressMap.setFitView();
                    }
                }else if (trackData.statusCode=="10"){
                    this.$toast("");
                    this.expressStatusMsgInFirstDiv='';
                    this.serviceMsg='';
                    this.isExpressStatus=false;
                    this.shopPosition = [Number(trackData.latFormatList[0].lng), Number(trackData.latFormatList[0].lat)];
                    this.markonMap(this.shopPosition,this.shopContent);
                    this.expressMap.setZoomAndCenter(14,this.shopPosition);
                }else if(trackData.statusCode=="5"){
                    this.$toast("");
                    this.expressStatusMsgInFirstDiv='';
                    this.serviceMsg='';
                    this.isExpressStatus=false;
                    this.shopPosition = [Number(trackData.latFormatList[0].lng), Number(trackData.latFormatList[0].lat)];   //
                    this.markonMap(this.shopPosition,this.shopContent);
                    this.expressMap.setZoomAndCenter(14,this.shopPosition);
                }
            },
            resolveExpressStatusList(){
                this.historyStatusDTOList.forEach((item,index)=>{
                    let para={
                        imgUrl:'',
                        expressStatusTitle:'',
                        expressStatusMsg:'',
                        updateTime:'',
                        textColor:'',
                        deliveryInfo:{},
                    };
                    switch (item.code) {
                        case "1":
                            break;
                        case "2":
                            para.imgUrl =index===0 ? require("../../assets/images/mall/express/icon_Orders_cut.png"):require("../../assets/images/mall/express/icon_Orders_cutwhite.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg='';
                            if (item.transporterName && item.transporterPhone){
                                para.deliveryInfo.transporterName=': '+item.transporterName;
                                para.deliveryInfo.transporterPhone=item.transporterPhone;
                            }
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "3":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_Delivery_cut.png") : require("../../assets/images/mall/express/icon_Delivery_cut_white.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "4":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_Orders_cut.png") : require("../../assets/images/mall/express/icon_Orders_cutwhite.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "5":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_cancel_cut.png") : require("../../assets/images/mall/express/icon_cancel_cut_white.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "9":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_abnormal_cut.png") : require("../../assets/images/mall/express/icon_abnormal_cut_white.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "10":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_abnormal_cut.png") : require("../../assets/images/mall/express/icon_abnormal_cut_white.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        case "100":
                            para.imgUrl = index===0 ? require("../../assets/images/mall/express/icon_Toshop_cut.png") : require("../../assets/images/mall/express/icon_Toshop_cut_white.png");
                            para.expressStatusTitle="";
                            para.expressStatusMsg="";
                            para.updateTime=item.updateTime;
                            para.textColor= index===0 ? '#333333': '#999999';
                            this.expressStatusShowData.push(para);
                            break;
                        default :
                            break;
                    }
                });
            },
            getDadaOrderTrackService(subBillNo){
                getDadaOrderTrack(subBillNo).then( result => {
                    this.isNetworkError=false;
                    if (result.resultCode=="0"){
                        this.trackData = result.data;
                        this.shopInfo=result.data.shopInfoDTO;
                        this.shopPicurl=this.shopInfo.shopPicUrl;
                        var backgroundUrl=require("../../assets/images/mall/express/shop_bg_cut.png");
                        var positionPointUrl=require("../../assets/images/mall/express/position_cut.png");
                        this.shopContent = '' +
                            '<div class="custom-content-marker " id="shopContent" style="height:68px;width:60px;position: relative">' +
                            '   <img src="'+positionPointUrl+'" style="height: 80px;width: 70px;position: absolute">'+
                            '   <img src="'+backgroundUrl+'" style="height: 80px;width: 70px;position: absolute">'+
                            '   <img src="'+this.shopPicurl+'" style="height: 48px;width: 48px;position: absolute; border-radius:40px;top: 10px;left: 10.5px">'
                            + '</div>';
                        this.historyStatusDTOList=result.data.historyStatusDTOList;
                        this.resolveTrackData(result.data);
                        this.resolveExpressStatusList();
                    }else {
                        console.log(result);
                        if (this.isMapInfo){
                            this.$toast("");
                        }
                        this.isBackSocketError=true;
                    }
                    this.expressloading=false;
                }).catch(error => {
                    console.log("  "+error);
                    this.expressloading=false;
                    if (error==''){
                        this.isNetworkError=true;
                    }else {
                        this.isBackSocketError=true;
                        if (this.isMapInfo){
                            this.$toast("");
                        }
                    }
                });
            },
            // 
            callShop(phone) {
                //   window.location.href = "tel:" + phone;
                // },
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                    window.location.href = "tel:" + phone;
                } else {
                    Dialog.confirm({
                        title:"",
                        message: phone,
                    }).then(() => {
                        window.location.href = "tel:" + phone;
                    }).catch(() => {
                        // on cancel
                        Dialog.close;
                    });
                }
            },
            //
            onRefresh() {
                let that = this;
                if (this.$route.params.expressCompany){
                    if (this.$route.params.expressCompany.indexOf('') != -1){
                        this.isMapInfo=true;
                        this.getDadaOrderTrackService(this.$route.params.subBillNo);
                    }else if(this.$route.params.expressCompany.indexOf('') != -1){
                        this.subbillno=this.subBillNo;
                        console.log("  "+this.subbillno);
                        this.isMapInfo=false;
                    }
                }else {
                    this.expressloading=false;
                    this.isNetworkError=true;
                }
                loadMap()
                    .then(AMap => {
                        console.log("")
                        that.expressMap = new AMap.Map('GDMap',{
                            zoom: 10,
                            center:[120.382689,36.066971],
                            animateEnable:true,
                            resizeEnable:true,
                        });
                        that.driving = new AMap.Driving({
                            map: this.expressMap,
                            showTraffic:false,
                            autoFitView:true,
                            hideMarkers:true,
                        });
                        that.expressloading=false;
                    })
                    .catch(() => {
                        that.isNetworkError=true;
                        console.log('')
                    })
            },
        },
    }
</script>

